@keyframes background_pop_color {
  0% {
    color: #fff; }
  20% {
    color: #00F0F6; }
  40% {
    color: #DA015C; }
  60% {
    color: #F61D64; }
  80% {
    color: #F6F400; }
  100% {
    color: #7EF600; } }

html {
  box-sizing: border-box; }

*, *:before, *:after {
  box-sizing: inherit; }

body {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  background: #2D2241; }

.editor {
  max-width: 544px;
  width: 100%; }
  .editor__heading {
    font-style: normal;
    font-weight: 200;
    font-size: 44px;
    line-height: 48px;
    color: #fff;
    font-family: 'Pacifico', cursive;
    margin: 0;
    margin-bottom: 48px; }
    .editor__heading:hover {
      animation: background_pop_color 5s ease-out infinite; }

.form__actions {
  margin-top: 48px;
  display: flex;
  justify-content: space-between; }

.form__label {
  display: block;
  font-size: 14px;
  margin-bottom: 8px;
  color: #fff;
  font-family: 'Roboto Mono', monospace;
  transition: transform 1s ease-in-out; }
  .form__label:hover {
    transform: scale(2); }

.form__textarea {
  width: 100%;
  border-radius: 8px;
  background: #fff;
  min-height: 30vh; }

.form__button {
  border: 1px solid #121232;
  border-radius: 4px;
  padding: 16px;
  min-width: 200px;
  font-weight: 600;
  letter-spacing: 0.5px; }
  .form__button--save {
    background: #043619;
    transition: background .3s linear,  transform 2s ease-in-out; }
    .form__button--save:hover {
      background: #27AE60;
      transform: rotate(-360deg); }
  .form__button--load {
    background: #003173;
    transition: background .3s linear, transform 2s ease-in-out; }
    .form__button--load:hover {
      background: #2F80ED;
      transform: rotate(360deg); }
